
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
header {
  margin-bottom: 25px;
  background-color: rgba(0, 0, 200, 0.65);
}
header h1 {
    margin: 0;
    color: #FFF;
    font-weight: lighter;
}
.error-text {
  color: rgba(255, 0, 0, 0.65);
  font-size: 12px;
  font-weight: bold;
  margin-bottom: 5px;
}



